
import { mapState } from 'vuex'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'login-view',
components: {
XInput,
Group,
XButton,
XDialog,
XHeader
},
data () {
return {
form: {
phone: '',
password: '',
captcha: ''
},
captcha: ''
}
},
computed: mapState({
accountToken: state => state.user.accountToken
}),
created: function () {
},
methods: {
goRegistView: function () {
this.$router.push({path: '/regist'})
},
login: function () {
if (!this.form.phone || !this.form.password) {
this.$vux.toast.text('', 'middle')
return
}
this.$store.dispatch('login', this.form).then(res => {
if (res.data.success === true && res.data.code === '1') {
this.$vux.toast.text(res.data.message, 'middle')
this.$router.replace({path: '/home'})
} else {
this.$vux.toast.text(res.data.message, 'middle')
}
})
}
}
}



